import { useRef } from 'react';

interface ChannelAddInputProps {
  newChannelId: string;
  onChannelIdChange: (value: string) => void;
  onCreateChannel: () => void;
  onOpenImageModal: () => void;
}

export default function ChannelAddInput({
  newChannelId,
  onChannelIdChange,
  onCreateChannel,
  onOpenImageModal,
}: ChannelAddInputProps) {
  const inputRef = useRef<HTMLInputElement>(null);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    //    4 
    const numericValue = value.replace(/\D/g, '').slice(0, 4);
    onChannelIdChange(numericValue);
  };

  const handleCreateChannel = () => {
    onCreateChannel();
    //      
    setTimeout(() => {
      inputRef.current?.focus();
    }, 0);
  };

  return (
    <div className="channel-add-input">
      <input
        ref={inputRef}
        type="text"
        placeholder=" ID (4 )"
        value={newChannelId}
        onChange={handleInputChange}
        className="channel-input-small"
        onKeyDown={(e) => e.key === 'Enter' && handleCreateChannel()}
        maxLength={4}
        inputMode="numeric"
        pattern="[0-9]*"
      />
      <button onClick={handleCreateChannel} className="btn-primary"></button>
      <button onClick={onOpenImageModal} className="btn-add-channel">
         
      </button>
    </div>
  );
}
